		"image": "immigration-laicite",
		"intro": "Pour le FN, depuis plusieurs dcennies dj, le clivage essentiel est celui qui oppose <a href=\"http://www.monde-diplomatique.fr/2017/05/LAMBERT/57493\"> le parti des trangers contre le parti de la France </a>, et l&#39;immigration est le problme central et le cur de toute son argumentation. Marine Le Pen, dont le programme de 2012 prvoyait d&#39; <a href=\"http://www.frontnational.com/pdf/projet_mlp2012.pdf\"> interdire les manifestations de soutien aux clandestins </a>, voit l&#39;immigration comme un vritable  raz-de-mare , une <a href=\"http://www.lepoint.fr/politique/marine-le-pen-compare-l-afflux-de-migrants-aux-invasions-barbares-14-09-2015-1964773_20.php\"> invasion  comparable  celles des barbares du IVme sicle</a>.</p>\n<p><a href=\"http://www.lefigaro.fr/flash-actu/2017/03/20/97001-20170320FILWWW00355-le-pen-je-veux-arreter-l-immigration.php\"> Arrter l&#39;immigration lgale et illgale </a> est donc la grande priorit du FN. Avec la fermeture des frontires et le durcissement des conditions d&#39;accs  la nationalit, le parti veut instaurer la <a href=\"http://www.humanite.fr/la-verite-sur-le-programme-du-front-national-631681\">priorit nationale</a>. Pour  riger la citoyennet franaise en privilge  et empcher les immigrs d&#39;accder aux droits fondamentaux, au profit des franais  de souche , il veut d&#39;abord <a href=\"https://www.mediapart.fr/journal/france/200317/fn-la-priorite-nationale-une-doctrine-contraire-aux-fondements-republicains-0\">modifier la constitution</a>. Pour les quelques trangers qui parviendraient  rester sur le territoire, le FN parle d&#39; assimilation, un concept  dvelopp dans le contexte des guerres coloniales franaises au XIXe sicle , qui s&#39;appliquait donc aux  indignes  des colonies franaises, <a href=\"https://www.mediapart.fr/journal/france/230317/programme-du-fn-4-l-identite-nationale-machine-de-guerre-contre-l-islam\">d&#39;aprs lhistorien Yvan Gastaud</a>. Quant aux musulmans, vritables  ennemis intrieurs  pour ce parti, Marion Marchal-Le Pen expliquait fin 2015 qu&#39;ils devaient <a href=\"http://www.europe1.fr/politique/pour-marion-marechal-le-pen-les-musulmans-ne-peuvent-etre-francais-que-sous-condition-2629283\"> se plier aux murs et au mode de vie que linfluence grecque, romaine, et 16 sicles de chrtient ont faonns .</a>",
