		"fn": " [56] Augmentation des plafonds de donations dfiscaliss aux enfants<br> [47] Dfiscalisation des ventes de parts sociales et actions dans les PME<br> [75] Baisse de 10 % des trois premires tranches dimpt<br> [78] Une taxe contre lvasion fiscale",
		"fi": "Les baisses dimpts sont annonces <strong>sans logique densemble</strong> de rpartition de leffort. En particulier les impts des plus riches naugmenteront pas, et mme baisseront puisque ceux-ci pourront transmettre du capital au sein de la famille, ou vendre des parts dentreprises sans contribuer  leffort collectif.</p>\n<p>Lide dune taxe contre lvasion fiscale a quelque chose de risible, si lon tait pas certain que le Front National ne lavanait que pour lannonce, sans aucune intention de lutter contre la fraude fiscale, dont ses membres son coutumiers.",
		"aec": "<strong><a href=\"https://laec.fr/s37m1\">Crer un impt universel</a></strong> qui luttera de manire concrte contre l&#39;vasion fiscale : vivre  ltranger nempche pas de payer la diffrence entre les impts du pays daccueil et celui qui aurait t pay en France.<br><strong><a href=\"https://laec.fr/s36m1\">Lisser sur 14 tranches</a></strong> l&#39;impt sur le revenu pour rpartir l&#39;effort. La plus haute tranche sera  90%.<br><strong><a href=\"https://laec.fr/s19m3\">Instaurer une taxe sur les transactions financires</a></strong>",
