		"main": true,
		"image": "urgence-sociale",
		"intro": "Le Front National nous parle beaucoup de la prcarit des travailleurs, des retraits, du chmage ; il accuse le gouvernement sortant et se proclame le parti du Peuple.<br>Parmis les 144 points de Mme Le Pen, certains abordent des thmes importants de la situation sociale de la France. Nous voudrions revenir sur quelques-uns de ces points, car certaines positions dfendues par les lus du Front National dans les sances parlementaires de ces dernires annes sont en contradiction avec ce dont se rclame la  candidate du Peuple . Par ailleurs, il est malheureusement peu question dans son programme des incroyables ingalits sociales de plus en plus videntes aujourd&#39;hui.",
